import React, { useState } from 'react'
import MenuBookIcon from '@mui/icons-material/MenuBook';
import CircleOutlinedIcon from '@mui/icons-material/CircleOutlined';
import ClearOutlinedIcon from '@mui/icons-material/ClearOutlined';
import CheckIcon from '@mui/icons-material/CheckCircleOutlineOutlined';
import "./Container.css"
const Container = () => {
    const [check, setCheck] = useState(false);
    const [inputval, setInputval] = useState('');
    const [tasks, setTasks] = useState([]);
    const handletooggle = (index) => {
        setTasks(tasks.map((task, i) => i === index ? { ...task, completed: !task.completed } : task));
    }
    const handletasks = (e) => {
        setInputval(e.target.value);
    }
    const addtask = () => {
        if (inputval == '') {
            alert("Please Write an Task")
        }
        else {
            setTasks([...tasks, { text: inputval, completed: false }]);
            setInputval('');
        }
    };

    const deleteTask = (index) => {
        setTasks(tasks.filter((_, i) => i !== index));
      };
    return (
        <div className='container'>
            <div className="head">
                <h1>To-Do List</h1>
                <MenuBookIcon style={{ width: '40px', height: '40px' }} />
            </div>
            <div className="seconddown">
                <input value={inputval} onChange={handletasks} spellCheck="false" type="text" placeholder='Add your task...' />
                <button onClick={addtask} style={{ cursor: 'pointer' }}>ADD</button>
            </div>
            <div className="list">
                {tasks.map((task, index) => (
                    <div className="listi" key={index}>
                        <div className="flexy">
                            <div>
                                {!task.completed ? (
                                    <CircleOutlinedIcon onClick={() => handletooggle(index)} style={{ cursor: 'pointer' }} />
                                ) : (
                                    <CheckIcon onClick={() => handletooggle(index)} style={{ cursor: 'pointer' }} />
                                )}
                            </div>
                            <p style={{textDecoration: task.completed ? 'line-through' : 'none'}} >{task.text}</p>
                        </div>
                        <ClearOutlinedIcon onClick = {() => deleteTask(index)} style={{ cursor: 'pointer' }} />
                    </div>
                ))}
            </div>


        </div>
    )
}

export default Container
